<template>
  <div id="app">

    <header id="header">
      <h1 class="title"><font-awesome-icon icon="map-marker-alt" /> 부산광역시 남구 용호로 232번길</h1>
    </header>
    
    <div id="content">
      <transition name="slide"
        v-on:before-enter="beforeEnter"
        v-on:after-enter="afterEnter">
        <router-view/>
      </transition>
    </div>
    
    <footer id="footer">
      <ul>
        <li><router-link to="/"><font-awesome-icon icon="home" /><span>홈</span></router-link></li>
        <li><router-link to="/near"><font-awesome-icon icon="compass" /><span>내근처</span></router-link></li>
        <li><router-link to="/area"><font-awesome-icon icon="map-marker-alt" /><span>지역</span></router-link></li>
        <li><router-link to="/"><font-awesome-icon icon="ticket-alt" /><span>쿠폰</span></router-link></li>
        <li><router-link to="/"><font-awesome-icon icon="user" /><span>내정보</span></router-link></li>
      </ul>
    </footer>
  </div>
</template>

<script>
import Vue from 'vue'
import { Carousel } from 'bootstrap-vue/es/components'

Vue.use(Carousel)

export default {
  name: 'App',
  methods:{
    beforeEnter: function(){
      console.log('전환시작');
    },
    afterEnter: function(){
      console.log('전환완료');
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#header, #footer {
  position: fixed;
  font-size:20px;
  text-align:center;
  line-height:45px;
}

#header {
  width:100%;
  height:50px;
  top:0; left:0;
  border-bottom:1px solid #ddd;
  z-index:999;
  background-color:#fff;
}

#header .title{font-size:20px;}

#content {
  position: absolute;
  top:50px;
  bottom:50px;
  width:100%;
  overflow-y:auto;
  overflow-x:hidden;
}

#footer {
  width:100%;
  bottom:0;
  height:50px;
  background-color:#fff;
  border-top:1px solid #ddd;
  z-index:999;
}

#footer ul{
  display: flex;
}

#footer ul li{
  flex-basis: 25%;
  font-size:1rem;
  line-height:.8rem;
  padding:8px;
}

#footer ul li a{
  text-decoration: none;
  color:#2c3e50;
  display:block;
  width:100%;
  height:100%;
}

#footer ul li span{
  font-size:.7rem;
  display:block;
  margin-top:4px;
}



.slide-enter-active, .slide-leave-active{
  transition: transform .5s;
}

.slide-leave-to{
  transform: translateX(-200%)
}

.slide-leave-active{
  position: absolute;
  top:0;
}

.slide-leave{
  position: absolute;
  top:0;
}

.slide-enter{
  position: relative;
  transform: translateX(200%)
}

.content{
  position:relative;
}


body{overflow-x:hidden;}

@keyframes slide-out {
  0% {transform: translateX(0); }
  100% {transform: translateX(-200%); }
} 
@keyframes slide-in {
  0% {transform: translateX(-200%); }
  100% {transform: translateX(0); }
}

</style>



