import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.use(Vuex)
Vue.use(VueAxios, axios)

const URL_AREAS = 'http://test.dibira.com/vue/get_areas.php';

export default new Vuex.Store({

    state:{
        shopList: [],
        areaList: {},
        selectedAreaIndex: 0
    },

    getters:{
        getShopList(state){
            return state.shopList;
        },
        getAreaList(state){
            return state.areaList;
        },
        getSelectedAreaIndex(state){
            return state.selectedAreaIndex;
        }
    },

    mutations:{

        clearShopList(state){
            state.shopList = [];
        },

        setShopList(state, payload){
            state.shopList = payload;
        },

        addShopList(state, payload){
            payload.forEach(function(el){
                state.shopList.push(el);
            });
        },

        setAreaList(state, payload){
            state.areaList = payload;
        },

        setSelectedAreaIndex(state, payload){
            state.selectedAreaIndex = payload;
        }
    },
    
    actions:{
        //TODO: AJAX 처리해야 함
        loadShopList(context){
            let arr = []
            for(let i=0; i<10; i++){
                
                arr.push({
                    idx: 1,
                    sangho: '가자마사지',
                    img: 'http://www.msgtong.com/_Manager/_upfiles/org/2018119.18247.554.jpg',
                    comment: 5,
                    star: 7.4,
                    favorite: 115,
                    lat: 55.5555,
                    long: 329.445,
                    addr: '부산 남구 용호동',
                    distance: '3.0km',
                    price: 70000,
                    sale_price: 30000,
                    sale_rate: 57
                });
                
            }
            context.commit('addShopList', arr);
        },

        loadAreas(context){
            Vue.axios.get('http://test.dibira.com/vue/get_areas.php')
            .then(function(result){
                context.commit('setAreaList', result.data);
            });
        }
    }

});


