import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Near from '@/components/Near'
import Area from '@/components/Area'
import Detail from '@/components/Detail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path: '/near',
      name: 'Near',
      component: Near
    },
    {
      path: '/area',
      name: 'Area',
      component: Area
    },
    {
      path: '/detail',
      name: 'Detail',
      component: Detail
    }
  ]
})



